@import url("https://fonts.googleapis.com/css?family=Oswald|Playfair+Display|Roboto:300,400,500|Tulpen+One&display=swap");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-back {
  width: 1em;
  height: 1em;
  fill: white;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: none;
}

.icon-hide {
  width: 1em;
  height: 1em;
  fill: white;
}

.icon-list {
  width: 1.33em;
  height: 1em;
  fill: white;
}

.icon-pause {
  width: 1em;
  height: 1em;
  fill: white;
}

.icon-photo {
  width: 1.2em;
  height: 1em;
  fill: white;
}

body {
  color: white;
  background: #000;
}

.logo {
  font-family: 'Playfair Display', serif;
  font-size: 38px;
  letter-spacing: -0.2;
  text-transform: uppercase;
  position: relative;
}

.logo:after, .logo:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  left: 0;
  background: white;
}

.logo:after {
  top: 5px;
}

.logo:before {
  bottom: 1px;
}

.control-button {
  background: rgba(255, 255, 255, 0.1);
  padding: 10px;
  height: 52px;
  width: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 12px;
  pointer-events: auto;
  cursor: pointer;
  position: relative;
}

.control-button i {
  position: relative;
  left: -2px;
}

.control-button:hover {
  background: rgba(255, 255, 255, 0.3);
}

.control-button__list:hover .planet-menu {
  display: block !important;
}

.top-side {
  display: flex;
  position: relative;
}

.top-side__back {
  font-family: 'Roboto', sans-serif;
  color: white;
  text-decoration: none;
  font-size: 18px;
  pointer-events: auto;
}

.top-side__icon-back {
  font-size: 14px;
}

.top-side__logo {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
}

.top-side__controls {
  margin-left: auto;
}

.planet-menu {
  display: none !important;
  position: absolute;
  top: 0;
  right: 100%;
  display: flex;
  flex-direction: column;
}

.planet-menu__list {
  max-height: 34vh;
  overflow: hidden;
  overflow-y: scroll;
  box-sizing: border-box;
}

.planet-menu__list::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.1);
}

.planet-menu__list::-webkit-scrollbar {
  width: 8px;
  background-color: rgba(255, 255, 255, 0.1);
}

.planet-menu__list::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}

.planet-menu__head {
  background: rgba(255, 255, 255, 0.3);
  font-size: 18px;
  line-height: 1.64;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 116px;
  white-space: nowrap;
  padding: 0px 50px 0px 30px;
  font-family: 'Oswald', sans-serif;
}

.planet-menu__item {
  background: rgba(255, 255, 255, 0.1);
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  opacity: 0.8;
  padding: 15px 50px 15px 30px;
}

.planet-menu__item:hover {
  background: rgba(255, 255, 255, 0.3);
}

.mid-side {
  margin-top: auto;
  margin-bottom: -160px;
}

.about-planet__head {
  font-family: 'Oswald', sans-serif;
  font-size: 26px;
  letter-spacing: 2.5%;
  line-height: 1.33;
  margin-bottom: 30px;
  text-transform: uppercase;
  font-weight: 600;
}

.about-planet__text {
  font-family: 'Roboto', sans-serif;
  line-height: 1.74;
  font-size: 16px;
  letter-spacing: 0.2;
  max-width: 295px;
  font-weight: 300;
  opacity: 0.6;
  max-height: 310px;
  overflow: scroll;
  overflow-x: hidden;
  padding-right: 25px;
  pointer-events: auto;
}

.about-planet__text::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: transparent;
}

.about-planet__text::-webkit-scrollbar {
  width: 8px;
  background-color: transparent;
}

.about-planet__text::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}

.bottom-side {
  display: flex;
  justify-content: space-between;
  margin-top: auto;
}

.planet-name {
  font-family: 'Tulpen One', cursive;
  font-size: 140px;
  align-self: flex-end;
  position: relative;
  bottom: -28px;
  width: 280px;
  position: relative;
  height: 140px;
  overflow: hidden;
}

.planet-name__item {
  position: absolute;
  transform: translate(0, -120%);
}

.planet-name__item_active {
  transform: translate(0, 0%);
}

.planet-data {
  margin-right: auto;
  margin-left: 2vw;
  display: flex;
}

.planet-data__item {
  display: flex;
  align-items: flex-end;
  margin-right: 60px;
}

.planet-data__item:last-child {
  margin-right: 0;
}

.planet-data__name {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  margin-right: 10px;
}

.planet-data__value {
  font-size: 46px;
  position: relative;
  bottom: -5px;
  font-family: 'Tulpen One', cursive;
}

.service {
  text-align: right;
}

.service__controls {
  margin-bottom: 45px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.service__tip {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 2.5%;
  opacity: 0.5;
}

.service__tip:last-child {
  margin-top: 10px;
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  margin: 0;
  padding: 0;
  width: 100vw;
  overflow: hidden;
}

.ui-container {
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 6vh 7vw;
  width: 100%;
  min-width: 1110px;
}
